$color__font--white: #fff;
$font__weight--thin: 100;
$font__weight--light: 300;
$font__weight--regular: 400;
$font__weight--medium: 500;
$font__weight--bold: 700;

$color--yellow: #ffba00;
$color--orange: #f34d04;
$color--blue-light: #089dea;
$color--green: #54d154;
$color--green-light: #e1f3e1;
$color--green-deep: #2b9b2b;
$color--green-medium: #3eba3e;
$color--white: #fff;
$color--grey: #828181;
$color--light-grey: #b7b3b3;
$color--soft-grey: #cccccc;
$color--melange-grey: #f2f1f1;
$color--soft-black: #333333; 
$color--black: #222222;

.container {
	position: relative;
	width: 100%;
	max-width: 1170px;
	overflow: hidden;
	margin: 0 auto;
	&--green {
		background-color: $color--green;
	}
	&--green-medium {
		background-color: $color--green-medium;
	}
	&--melange-grey {
		background-color: $color--melange-grey;
	}
	&--soft-black {
		background-color: $color--soft-black;
	}
	&--black {
		background-color: $color--black;
	}
}


.header {
	position: fixed;
	width: 100%;
	max-width: 1170px;
	height: 35px;
	overflow: hidden;
	margin: 0;
	z-index: 10;
	transition: 0.6s ease-in-out;
	&--parallax {
		background-color: rgba(0, 0, 0, 0.5);
	}
}

.toggle-button {
	display: none;
	float: right;
	width: 30px;
	height: 21px;
	background-image: url(../images/burger.svg);
	margin: 7px 5px 0 30px;
	cursor: pointer;
}

.nav-menu {
	display: none;
	float: left;
	width: 50%;
	margin: 10px 0 0 30px;
	min-width: 385px;
	font-family: 'Roboto', sans-serif;
	&__button {
		margin-right: 20px;
		color: $color__font--white;
		font-size: 16px;
		text-decoration: none;
		font-weight: $font__weight--light;
		opacity: 0.8;
		&:hover {
			font-weight: $font__weight--medium;
			opacity: 1;
		}
	}
	&__button--active {
		font-weight: $font__weight--regular;
		border-top: 5px solid $color--green;
		opacity: 1;
	}
}

.logo {
	position: absolute;
	float: left;
	width: 20%;
	left: 50%;
	margin-left: -35px;
	width: 70px;
	height: 30px;
	background: url(../images/sprite-icons.png);
	background-position: 614px 1420px;
}

.search{
	position: relative;
	float: right;
	width: 30%;
	margin-right: 30px;
	&:after {
		display: block;
		position: absolute;
		right: 0;
		top: 0;
		content: '';
		width: 30px;
		height: 30px;
		background: url(../images/sprite-icons.png);
		background-position: 674px 1420px;
	}
	.search__input {
		float: right;
		height: 27px;
		padding: 0 6px;
		margin-top: 5px;
		border-radius: 25px;
		font-family: 'Roboto', sans-serif;
		border: none;
		background-color: rgba(230, 229, 229, 0.9);
	}
}

@media screen and (min-width: 981px) {
	.nav-menu {
		display: block !important;
	}
}

@media screen and (max-width: 320px) {
	.search {
		position: absolute;
		left: 50%;
		width: 130px;
		margin: 0 0 0 -58px;
	}
	.search .search__input {
		width: 100%;
	}
	.logo {
		left: 5px;
		margin-left: 0;
	}
}

@media screen and (min-width: 321px) and (max-width: 660px) {
	.search{
		position: absolute;
		left: 50%;
		width: 180px;
		margin: 0 -85px;
	}
	.search .search__input {
		width: 100%;
	}
	.logo {
		left: 5px;
		margin-left: 0;
	}
}

@media screen and (max-width: 980px) {
	.header {
		height: auto;
		min-height: 37px;
	}

	.search {
	
	}

	.toggle-button {
		display: block;

	}
	.nav-menu {
		overflow: hidden;
		min-width: 100%;
		width: 100%;
		margin: 10px 0 0 0;
		transition: display ease-out 600ms;
		&__button {
			display: block;
			min-width: 70px;
			margin: 0;
			padding: 5px 0;
			text-align: center;
		}
	}
}












