.herospace {	
	position: relative;
	width: 100%;
	height: 660px;
	margin-top: -35px;
	overflow: hidden;
	&__main-container {
		position: absolute;
		width: 100%;
		bottom: 0;
	}
	&__background-img {
		height: 660px;
		background: url(../images/herospace.png);
		background-size: cover;
		background-position: center;
	}
	&__title {
		text-align: center;
		color: $color__font--white;
		font-size: 55px;
	}
}

.button {
	display: block;
	margin: 0 auto;
	font-family: 'Roboto', sans-serif; 
	border-radius: 30px;
	border: none;
	&--lg {
		width: 210px;
		height: 50px;
		text-transform: uppercase;
		font-weight: $font__weight--thin;
	}
	&--md {
		padding: 11px 33px;
		font-weight: $font__weight--thin;
	}
	&--sm {
		padding: 5px 21px;
		font-size: 16px;
		font-family: 'Montserrat', sans-serif;
	}
	&--green {
		background-color: $color--green;
		color: $color--white;
		font-weight: $font__weight--light;
	}
	&--deep-green {
		background-color: $color--green-deep;
		color: $color--white;
		font-weight: $font__weight--light;
	}
	&--grey {
		background-color: #dddcdc;
		color: #aca5a5;
	}
	&--main {
		margin-top: 40px;
		font-size: 22px;
	}
	&--white {
		background-color: $color--white;
	}
	&--scroll {
		margin-top: 215px;
		margin-bottom: 10px;
		padding: 3px 25px 3px 10px;
		font-size: 16px;
		background-color: rgba(255, 255, 255, 0.7);
		&:after {
			position: absolute;
			content: '';
			width: 20px;
			height: 20px;
			background: url(../images/sprite-icons.png);
			background-position: 55px 1408px;
		}
	}
}

.headline {
	margin: 40px 0 60px;
	text-align: center;
	&__title {
		margin:  0  0 5px;
		letter-spacing: 2px;
		text-transform: uppercase;
		font-family: 'Montserrat', sans-serif;
		font-size: 21px;
		color: $color--grey;
		font-weight: $font__weight--light;
	}
	&__line {
		display: block;
		margin: 0 auto;
		width: 45px;
		border-top: 2px solid $color--grey;
	}
	&__description {
		margin-top: 5px;
		color: $color--light-grey;
		font-family: 'Montserrat', sans-serif;
		font-weight: $font__weight--light;
		&--sm {
			font-size: 16px;

		}
		&--xs {
			font-size: 14px;
		}
		&--full-text {
			text-align: center;
			font-size: 15px;
			padding: 0 10px;
		}
		&--adress {
			font-size: 15px;
			padding: 0 20px;
		}
	}
	&--green {
		.headline__title,
		.headline__description {
			color: $color--green-deep;
		}
		.headline__line {
			border-color: $color--green-deep;
		}
	}
	&--margin-medium {
		margin: 40px 0 50px;
	}
	&--margin-small {
		margin: 0;
		.headline__title {
			font-size: 16px;
			color: #b7b3b3;
			&--black {
				margin: 25px 0 0;
				color: #101010;
				opacity: 0.8;
				font-weight: $font__weight--regular;
			}
		}
		.headline__line {
			width: 35px;
			margin-bottom: 20px;
			color: #b7b3b3;
			&--black {
				border-color: #101010;
			}
		}
	}
}

.touts-container {

}

.tout {
	float: left;
 	width: calc(100% / 3);
 	margin-bottom: 75px;
 	text-align: center; 
}

.tout-header {
	&__img-container {
		position: relative;
		margin: 0 auto;
		width: 140px;
		height: 140px;
		background-color: $color--green-light;
		border-radius: 30px;
	}
	&__img {
		position: absolute;
		top: 50%;
		margin-top: -39px;
		left: 50%;
		margin-left: -44px;
		width: 88px;
		height: 78px;
		background: url(../images/sprite-icons.png);
		
		&--cam {
			background-position: 652px 1335px;
			transition: 0.5s ease-out;
			&:hover {
				background-position: 652px 1222px;
			}
		}
		&--clock {
			background-position: 503px 1335px;
			transition: 0.5s ease-out;
			&:hover {
				background-position: 503px 1222px;
			}
		}
		&--suitcase {
			background-position: 355px 1335px;
			transition: 0.5s ease-out;
			&:hover {
				background-position: 355px 1222px;
			}
		}
	}
}

.tout-body {
	padding: 0 50px;
	margin: 25px 0;
	font-family: 'Montserrat', sans-serif;
	color: $color--grey;
	&__title {
		font-size: 24px;
		font-weight: $font__weight--regular;
	}
	&__text {
		font-size: 15px;
		font-weight: $font__weight--light;
	}
}

.tout-footer {

}

.photo-section {
	margin: 0 auto 25px;
	overflow: hidden;
	&__button-container {
		display: block;
		width: 281px;
		margin: 0 auto;
		margin-bottom: 60px;
		overflow: hidden;
		box-sizing: border-box;
	}
	&__button {
		float: left;
		margin: 0 auto;
		padding: 5px 0;
		border: 1px solid $color--green-deep;
		background-color: $color--white;
		font-size: 16px;
		font-weight: $font__weight--light;
		text-align: center;
		text-decoration: none;
		color: $color--green-deep;
		box-sizing: border-box;

		&:nth-of-type(1) {
			border-radius: 30px 0 0 30px;
		}
		&:nth-of-type(3) {
			border-radius: 0 30px 30px 0;
		}
		&--active {
			background-color: $color--green-deep;
			color: $color--white;
		}
		&--start {
			width: 121px;
		}
		&--middle {
			width: 90px;
		}
		&--finish {
			width: 70px;
		}
	}
	&__container {
		margin: 0 125px;
		overflow: hidden;
	}
	&__photo {
		width: calc((100% - 138px) / 5);
		margin: 0 0 30px 30px;
		box-sizing: border-box;
		&:nth-of-type(1),
		&:nth-of-type(6n) {
			margin-left: 0;
		}
	}
	&__more-button {
		margin-bottom: 60px;
		padding: 10px 35px 10px 15px;
		text-align: left;
		&:after {
			position: absolute;
			content: '';
			width: 20px;
			height: 20px;
			background: url(../images/sprite-icons.png);
			background-position: 506px 1406px;
		}
	}
}

.carousel-section {
	width: 100%;
	overflow: hidden;
	padding-bottom: 60px;
}

.carousel {
	width: 100%;
	max-width: 500px;
	margin: 0 auto;
	padding: 0 10px;
	text-align: center;
	color: #5d5d5d;
}

.info-ceo {
	position: relative;
	display: block;
	width: 200px;
	margin: 0 auto;
	overflow: hidden;
	&__pic {
		float: left;
		width: 45px;
		height: 45px;
		background: url(../images/sprite-icons.png);
		background-position: 296px 1423px;
		border: 2px solid #e1e1e1;
		border-radius: 50%;
	}
	&__container {
		float: left;
		margin: 5px 0 0 10px; 
		font-family: 'Montserrat', sans-serif;
	}
	&__name {
		font-size: 16px;
		font-weight: $font__weight--light;
		color: #5d5d5d;
		margin: 0;
	}
	&__charge {
		margin: 0;
		color: $color--soft-grey;
		letter-spacing: 1px;
	}
}

/*slick styles*/

.slick-next:before {
    position: absolute;
	content: '';
	width: 25px;
	height: 25px;
	right: -50px;
	top: 0px;
	background: url(../images/sprite-icons.png);
	background-position: 360px 1408px;
}

.slick-prev:before, {
	position: absolute;
	content: '';
	width: 25px;
	height: 25px;
	left: -40px;
	top: 0px;
	background: url(../images/sprite-icons.png);
	background-position: 415px 1408px;
}



.client-section {
	overflow: hidden;

	&__container {
		margin: 0px 150px;
		overflow: hidden;
	}

	&__logo {
		float: left;
		width: calc(100% / 4);
		height: 80px;
		margin-bottom: 25px;
		background: url(../images/sprite-icons.png);
		box-sizing: border-box;
		&--google {
			background-position: 216px 1376px;	
		}
		&--yahoo {
			background-position: 226px 1311px;	
		}
		&--microsoft {
			background-position: 220px 1237px;	
		}
		&--intel {
			background-position: 226px 1157px;	
		}
		&--qualcomm {
			background-position: 226px 1060px;	
		}
		&--cisco {
			background-position: 226px 967px;	
		}
		&--apple {
			background-position: 226px 867px;	
		}
		&--asus {
			background-position: 226px 790px;	
		}
	}
	&__button {
		position: relative;
		font-size: 18px;
		font-weight: $font__weight--light;
		margin-bottom: 65px;
		margin-top: 20px;
		padding: 8px 45px 8px 25px;
		&:after {
			position: absolute;
			content: '';
			width: 20px;
			height: 20px;
			right: 15px;
			background: url(../images/sprite-icons.png);
			background-position: 463px 1408px;
		}
	}
}

.team-section {
	margin: 0 150px;
	overflow: hidden;
}

.tout-member {
	float: left;
	width: calc((100% - 250px) / 5);
	max-width: 125px;
	overflow: hidden;
	margin: 0 25px 45px;
	box-sizing: border-box;
	&__pic {
		display: block;
		width: 100%;
		max-width: 125px;
		margin: 0 auto;

	}
	&__line {
		width: 2px;
		height: 80px;
		margin: 10px auto;
		background-color: #207b20;
	}
	&__name {
		text-align: center;
		color: $color--white;
		font-family: 'Montserrat', sans-serif;
		font-size: 14px;
		font-weight: $font__weight--light;
	}
}

.social-box {
	position: relative;
	margin: 10px auto;
	width: 86px;
	height: 28px;
	border-radius: 30px;
	background-color: rgba(255, 255, 255, 0.7);
	&__icon {
		position: absolute;
		width: 24px;
		height: 24px;
		left: 50%;
		margin-left: -12px;
		background: url(../images/sprite-icons.png);
		background-position: 166px 1418px;
		&:before {
			display: block;
			content: '';
			position: absolute;
			width: 24px;
			height: 24px;
			left: -27px;
			background: url(../images/sprite-icons.png);
			background-position: 208px 1417px;	
		}
		&:after {
			display: block;
			content: '';
			position: absolute;
			width: 24px;
			height: 24px;
			left: 27px;
			background: url(../images/sprite-icons.png);
			background-position: 119px 1417px;	
		}	
	}	
}

.work-with-us-button {
	margin: 30px auto 25px;
	text-transform: uppercase;
	box-shadow: -2px 5px 5px $color--green-deep;
}

.other-info-section {

}

.about,
.recent,
.contact {
	float: left;
	width: calc((100% - 180px) / 3);
	margin: 30px; 
	color: #d2d2d2;
}

.tag-box {
	overflow: hidden;
	margin: 0 0 12px 0;
	font-family: 'Montserrat', sans-serif;
	&__pic {
		float: left;
	}
	&__text-container {
		float: left;
		margin: 2px 0 0 7px;
		font-weight: $font__weight--light;
		color: #b7b3b3;
	}
	&__text {
		font-size: 15px;
		text-transform: capitalize;
	}
	&__tag {
		float: left;
		padding: 1px 5px;
		margin-top: 5px;
		border-radius: 3px;
		font-size: 12px;
		text-align: center;
		text-transform: uppercase;
		color: $color--white;
		font-weight: $font__weight--thin;
		&--green {
			background-color: $color--green;
		}
		&--yellow {
			background-color: $color--yellow;
		}
		&--blue-light {
			background-color: $color--blue-light;
		}
		&--orange {
			background-color: $color--orange;
		}
	}
}

.contact {

}

.form-contact {
	width: 100%;
	margin: 25px 0 0 0;
	font-family: 'Montserrat', sans-serif;
	font-weight: $font__weight--light;
	font-size: 14px;
	&__input {
		width: calc(100% - 20px);
		height: 20px;
		background-color: #444444;
		margin: 0 0 2px 0;
		padding: 5px 10px;
		border: none;
		&:nth-of-type(1) {
			border-radius: 3px 3px 0 0;
		}
	}
	&__textarea {
		width: calc(100% - 20px);
		height: 120px;
		background-color: #444444;
		margin: 0;
		padding: 5px 10px;
		border: none;
	}
	&__button {
		width: 100%;
		padding: 8px 0;
		border: none;
		background-color: $color--green-deep;
		text-transform: uppercase;
		font-size: 12px;
		color: $color--white;
		border-radius: 0 0 3px 3px;

	}
}

.media-section {
	margin: 0 0 30px;
	&__logo {
		margin-left: 95px;
		display: inline-block;
		text-align: center;
	}
}

@media screen and (max-width: 335px) {
	.tag-box {
		position: relative;
		display: block;
		float: none;
		margin: 0 auto;

		&__pic {
			display: block;
			margin: 0 auto;
			
		}
		&__text-container {
			margin: 0 auto;
			float: none;
			overflow: hidden;
		}
		&__text {
			display: block;
			text-align: center;
		}
		&__tag {
			margin: 0 auto;
			float: none;
			max-width: 100px;
		}
	}
} 

@media screen and (max-width: 480px) { 
	.photo-section {
		&__photo {
			width: calc((100% - 35px) / 2);
			&:nth-of-type(even) {
				margin-left: 30px;
			}
			
			&:nth-of-type(odd) {
				margin-left: 0;
			}
		}
	}
	.carousel {
		max-width: 240px;
	}
	.slick-next:before {
		right: 0px;
	}
	.slick-prev:before {
		left: 10px;
	}

	.team-section {
		margin: 0 80px;
	}
	.tout-member {
		width: calc(100% - 50px);
		max-width: 100%;
	}
}

@media screen and (max-width: 600px) {
	.herospace {
		&__title {
			font-size: 42px;
		}
	}
	.headline {
		padding: 0 10px;
	}

	.tout {
		width: 100%;
	}
	.photo-section {
		&__container {
			margin: 0 30px;
		}
	}
}

@media screen and (max-width: 670px) { 
	.client-section {
		overflow: hidden;

		&__container {
			margin: 0 70px;
		}

		&__logo {
			display: block;
			float: none;
			width: 100%;
			max-width: 180px;
			margin: 0 auto;
			&--google,
			&--yahoo,
			&--microsoft,
			&--intel,
			&--qualcomm,
			&--cisco,
			&--apple,
			&--asus {
				background-position-x: 215px;
			}
		}
	}
	.about,
	.recent,
	.contact {
		width: calc(100% - 60px);
	}
	.tag-box {
		max-width: 300px;
		margin: 0 auto 12px;
	}
}

@media screen and (max-width: 980px) { 
	.herospace {	
		
	}
}


@media screen and (max-width: 1170px) { 
	.media-section {
		&__logo {
			display: block;
			margin: 30px auto;
		}
	}
}


@media screen and (min-width: 481px) and (max-width: 720px) { 
	.photo-section {
		&__photo {
			width: calc((100% - 69px) / 3);
			&:nth-of-type(6) {
				margin-left: 30px;
			}
			&:nth-of-type(4),
			&:nth-of-type(7),
			&:nth-of-type(10) {
				margin-left: 0;
			}
		}
	}
}

@media screen and (min-width: 481px) and (max-width: 600px) { 
	.carousel {
		max-width: 320px;
	}
	.team-section {
		margin: 0 150px;
	}
	.tout-member {
		width: calc(100% - 50px);
		max-width: 100%;
	}
}

@media screen and (min-width: 601px) and (max-width: 720px) { 
	.tout {
		width: 50%;
		&:nth-of-type(3) {
			width: 100%;
		}
	}
	.team-section {
		margin: 0 100px;
	}
	.tout-member {
		width: calc((100% - 100px) / 2);
		max-width: 100%
	}
}


@media screen and (min-width: 721px) and (max-width: 980px) {
	.photo-section {
		&__photo {
			width: calc((100% - 104px) / 4);
			&:nth-of-type(5),
			&:nth-of-type(9) {
				margin-left: 0;
			}
			&:nth-of-type(6) {
				margin-left: 30px;
			}
		}
	}
	.team-section {
		margin: 0 120px;
	}
	.tout-member {
		width: calc((100% - 150px) / 3);
		max-width: 100%;
	}
	
}


@media screen and (min-width: 671px) and (max-width: 800px) {
	.client-section {
		overflow: hidden;
		&__container {
			margin: 0 90px;
		}
		&__logo {
			width: calc((100% - 120px) / 2);
			margin: 0 30px;
		}
	}
}

@media screen and (min-width: 801px) and (max-width: 1050px) {
	.client-section {
		overflow: hidden;
		&__logo {
			width: calc(100% / 3);
		}
	}
	
}

@media screen and (min-width: 671px) and (max-width: 1000px) {
	.about,
	.recent,
	.contact {
		width: calc((100% - 120px) / 2);
	}
}



